<template>
  <div id="app">
     <div>
        <select v-model="color">
          <option disabled value="">צבע האזור</option>
          <option value="green">{{HEBREW_COLORS.green}}</option>
          <option value="yellow">{{HEBREW_COLORS.yellow}}</option>
          <option value="orange">{{HEBREW_COLORS.orange}}</option>
          <option value="red">{{HEBREW_COLORS.red}}</option>
        </select>
      </div>

      <div>
        גודל המבנה
        <input type="number" v-model="structure_size" />
      </div>
      <div>
        מספר כניסות ראשיות
        <input type="number" v-model="num_primary_enternces" />
      </div>
      <div>
        מספר כניסות משניות
        <input type="number" v-model="num_secondary_enternces" />
      </div>

      <div>
        <h2>{{result}} מתפללים</h2>
        <h2>{{num_groups}} קפסולות</h2>
      </div>
  </div>
</template>

<script>
import CONSTANTS from './CONSTANTS'

export default {
  data: function() {
    return {
      calc_params: {
      structure_size: 500,
      num_primary_enternces: 5,
      num_secondary_enternces: 0,
      color: CONSTANTS.COLORS.green,
    },
    structure_size: 500,
    num_primary_enternces: 5,
    num_secondary_enternces: 0,
    structure_open: true,
    color: CONSTANTS.COLORS.green,
    num_groups: 1,
    result: null
    }
    
  },
  mounted: function() {
    this.getResult();
  },
  watch: {
    color: function() {
        this.calc_params.color = this.color.toString();
        this.getResult();
      },
    structure_size: function() {
      this.calc_params.structure_size = +this.structure_size;
      this.getResult();
    },
    num_primary_enternces: function() {
      this.calc_params.num_primary_enternces = +this.num_primary_enternces;
      this.getResult();
    },
    num_secondary_enternces: function() {
      this.calc_params.num_secondary_enternces = +this.num_secondary_enternces;
      this.getResult();
    }
  },
  computed: {
    HEBREW_COLORS: function(){return CONSTANTS.HEBREW_COLORS},

  },
  methods: {
    getResult: function() {
      const max_people = this.calc_params.structure_size / 4; //Max people - one for every 4 sqM
      const factor =
        this.calc_params.num_primary_enternces * 3 +
        this.calc_params.num_secondary_enternces * 2;

      const temp_result = Math.floor(Math.round(
        Math.min(
          max_people,
          factor * CONSTANTS.GROUP_SIZE_IN_CLOSED_STRUCTURE[this.calc_params.color]
        )
      ));

      this.result = temp_result - temp_result % CONSTANTS.GROUP_SIZE_IN_CLOSED_STRUCTURE[this.calc_params.color];

      this.num_groups = Math.floor(Math.max(1,this.result / CONSTANTS.GROUP_SIZE_IN_CLOSED_STRUCTURE[this.calc_params.color]));
    }
  }
}
</script>

